* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  display: flex;
}

.field {
  text-align: center;
  margin: 0 1rem;
}

.label {
  font-size: 1rem;
  display: block;
}

.container {
  flex-direction: column;
  align-items: center;
  margin: 2rem;
  display: flex;
}

.countdown-timer {
  text-align: center;
  color: #333;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1rem;
  font-size: 3rem;
  font-weight: bold;
  display: flex;
}

.countdown-timer span {
  width: 4rem;
  height: 4rem;
  background-color: #fff;
  border-radius: 50%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .2);
}

.countdown-timer span:before {
  content: attr(data-value);
  color: #333;
  font-size: 2rem;
  font-weight: bold;
}

.btn {
  color: #fff;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: .5rem;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  font-weight: bold;
}

.btn:disabled {
  cursor: not-allowed;
  background-color: #999;
}

